/* Styles for the toggle button */
.search__box{
    margin-top: 50px !important;
  }
.toggle-button {
    color: #000;
    cursor: pointer;
    float: right;
    font-size: 20px;
    text-align: center;
    padding: 0px 1px;
}
.right-toggle-button {
    color: #000;
    cursor: pointer;
    float: left;
    font-size: 20px;
    text-align: center;
    width: 20px;
    border: 0;
    background: 0;
    padding-left: 5px;
    padding-bottom: 5px;
} 
  /* Styles for the sidebar when it's open */
  .sidebar__left.open {
    width: 400px; /* Adjust the width as needed */
    transition: width 0.3s; /* Add a smooth transition */
  }
  
  /* Styles for the sidebar when it's closed */
  .sidebar__left.closed {
    width: 20px;
    overflow: hidden;
    transition: width 0.3s; /* Add a smooth transition */
  }
 
  .drawing-board__container {
    width: 100% !important;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    overflow: hidden;
}
/* Default style for the RightBar component */
.sidebar__right {
    width: 400px; /* Default width */
    transition: width 0.3s; /* Add a smooth transition for the width */
  }
  
  /* Closed style for the RightBar component */
  .sidebar__right.closed {
    width: 18px; /* Narrow width when the "closed" class is applied */
  }
.sidebar__left{
    padding: 0 !important;
}
/* Style for the content inside the sidebar */
.left-bar-content {
  display: block; /* By default, the content is visible */
}

/* Hide the content when the sidebar is closed */
.sidebar__left.closed .left-bar-content{
  display: none;
}
.sidebar__left.closed .project-heading{
  display: none;
}
.sidebar__left.closed .first-sidebar{
  height: 55px;
  padding: 0;
  width: 15px;
}
.sidebar__left.closed .toggle-left-sidebar{
  margin-top: 0;
}

.takeoff__tab {
  display: block; /* By default, the content is visible */
}

/* Hide the content when the sidebar is closed */
.sidebar__right.closed .takeoff__tab {
  display: none;
}
.sidebar__right.closed .tablinks {
  display: none;
}
.sidebar__right.closed .tab {
  margin-top: 0 !important;
}



