.course-input{
  width: 48%;
    float: left;
    margin-right: 13px;
    margin-bottom: 10px;
}
.course-input:nth-child(even){
  margin-right:0 !important;
}
.course-input input, .course-input select{
  background-color: #f4f4f4;
    border: 1px solid #f4f4f4;
    border-radius: 5px;
}









