.length-icon {
  margin-top: 8px !important;
}

.total {
  width: 30%;
}

.total:before {
  height: 37px;
  display: block;
  width: 1px;
  content: '';
  position: absolute;
  top: -8px;
  right: 0px;
  border-right: 1px solid #d9d9d9;
}

.total:after {
  height: 37px;
  display: block;
  width: 1px;
  content: '';
  position: absolute;
  top: -8px;
  left: -20px;
  border-right: 1px solid #d9d9d9;
}

.number:before {
  height: 37px;
  display: block;
  width: 1px;
  content: '';
  position: absolute;
  top: -8px;
  right: 0px;
  border-right: 1px solid #d9d9d9;
}

.count:before {
  height: 37px;
  display: block;
  width: 1px;
  content: '';
  position: absolute;
  top: -8px;
  right: -19px;
  border-right: 1px solid #d9d9d9;
}

.count,
.number {
  width: 35%;
}

.length-image-icon,
.line,
.count-number,
.feet-text {
  width: 25%;
}

.length-image-icon img {
  margin-top: 2px;
}

.length-image-icon:before {
  height: 39px;
  display: block;
  width: 1px;
  content: '';
  position: absolute;
  top: -8px;
  right: 2px;
  border-right: 1px solid #d9d9d9;
}

.length-image-icon:after {
  height: 39px;
  display: block;
  width: 1px;
  content: '';
  position: absolute;
  top: -8px;
  left: -10px;
  border-right: 1px solid #d9d9d9;
}

.count-number:before {
  height: 39px;
  display: block;
  width: 1px;
  content: '';
  position: absolute;
  top: -8px;
  right: -2px;
  border-right: 1px solid #d9d9d9;
}

.feet-text:before {
  height: 39px;
  display: block;
  width: 1px;
  content: '';
  position: absolute;
  top: -8px;
  right: -8px;
  border-right: 1px solid #d9d9d9;
}

.table-template table {
  border-collapse: collapse;
  width: 100%;
}

.table-template th {
  background-color: #F4F7F9;
  text-align: left;
  padding: 8px;
  color: #083F4E;
  font-size: 13px;
}

.table-template td {
  border: 1px solid #ddd;
  text-align: left;
  padding: 8px;
  background-color: #F7FAFC;
  font-size: 13px;
}

.table-template tr:nth-child(even) {
  background-color: #fff;
}

