.text-center{
    text-align: center;
}
.margin-bottom{
    margin-bottom: 12px;
}
.text-red {
    color: red;
}
.margin-left {
    margin-left: 20px;
}
.align-end {
    float: right;
}
.print-button{
    color: white;
    background-color: blue;
    border-radius: 5px;
    padding-top: 8px;
    padding-bottom: 8px;
    padding-left: 11px;
    padding-right: 11px;
    cursor: pointer;
    text-align: center;
    border-color: #1319c2;
}
.active-file {
        color: #00A5B6;
    /* background: yellowgreen !important; */
}
.open-file {
    background-color: #00A5B6 !important;
    color: #fff !important;
}
.open-project-btn {
    background: #007be5;
    color: #fff;
    padding: 5px 10px;
    border: 1px solid #007be5;
    border-radius: 5px;
    margin-left: 10px;
    cursor: pointer;
}
.open-project-btn-project {
    background: #007be5;
    color: #fff;
    padding: 5px 10px;
    border: 1px solid #007be5;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 10px;
}
.open-project-btn:hover, .open-project-btn-project:hover{
    background-color: blue;
    border: 1px solid blue;
}
.project-content{
    display: block;
    margin: 0 auto;
    width: 80%;
}
.project-btn-content{
    padding: 0;
}
.project-btn{
    margin-top: 10px;
    margin-left: 0;
}
.popup__create__project{
    display: block;
    width: 100%;
    max-width: 500px;
    min-height: 260px;
    position: fixed;
    top: 30%;
    left: 50%;
    translate: -50% -50%;
    background-color: white;
    box-shadow: 0 0 12px rgba(0, 0, 0, 0.1);
    border-radius: 6px;
    border: 1px solid rgba(0, 0, 0, 0.1);
    z-index: 10;
}