.popup-length-takeoff {
  max-width: 940px !important;
}
.takeoff-name {
  width: 18%;
  float: left;
  margin-right: 17px;
}
.takeoff-name-main {
  width: 100%;
  display: inline-block;
  margin-bottom: 10px;
}
.additional_label_main label {
  margin-right: 1%;
  width: 16%;
  float: left;
  text-align: center;
}
.additional_label_main label:last-child {
  margin-right: 0 !important;
  width: auto !important;
  float: none !important;
  text-align: center;
}
.additional-material-add-btn {
  background: blue;
  border-color: blue;
  border-radius: 3px;
  color: #fff;
  padding: 5px 6px;
}
.anchor-spacing {
  width: 34%;
}
.takeoff-name-main .takeoff-name:last-child {
  margin-right: 0 !important;
}
.takeoff-height {
  width: 33%;
  float: left;
}
.height-inputs {
  width: 48%;
  float: left;
  margin-right: 5px;
}
.additional-add-btn {
  margin-top: 28px;
  color: white;
  background: blue;
  padding: 5px 6px;
  border-color: blue;
  border-radius: 3px;
}
.additional-delete-btn {
  color: white;
  background: red;
  padding: 5px 12px;
  border-color: red;
  border-radius: 3px;
}
.takeoff-additional {
  width: 16%;
}
.shape-main {
  width: 100%;
  float: left;
  margin-top: 5px;
}
.shape-field {
  width: 49%;
  float: left;
  margin-right: 1px;
}
.takeoff-double {
  width: 18%;
  float: left;
  margin-right: 17px;
}
.takeoff-double input {
  float: left;
  width: 48%;
  margin-right: 1px;
}
.action-delete-icon {
  min-width: inherit !important;
}
.mr {
  margin-right: 10px;
}
.use-btn {
  color: #ffff;
  background-color: #00a5b6;
}
.used-btn {
  color: #ffff;
  width: 37px;
  background: #083f4e;
}
.btn-delete {
  background-color: white !important;
  color: crimson !important;
}
.btn-edit {
  color: #007be5;
  background-color: white;
}
.action-th {
  width: 150px;
}
.lineal-additional {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 5px;
  width: 100%;
}
.w-20 {
  width: 20% !important;
}
.spacing .takeoff-additional {
  width: 31% !important;
}
.per-sq-ft .takeoff-additional {
  width: 48% !important;
}
.mt-20px {
  margin-top: 20px;
}
