.tool__box-header {
  padding-block: 0.4rem !important;
}
table {
  margin-block: 0.2rem !important;
}
td,
th {
  padding: 2px 16px !important;
}
td {
  vertical-align: middle;
}
.trash-icon {
  color: red;
  cursor: pointer;
}
.temp-edit {
  color: #00a5b6;
  display: inline-block;
  font-size: 12px;
  outline: 0;
  padding: 4px 5px;
  min-width: 22px !important;
  border-radius: 3px;
  background-color: #F7FAFC
}
.temp-edit:hover {
  color: #006772;
  cursor: pointer;
  background-color: #f6f6f6
}
.temp-use {
  border: 1px solid #00a5b6;
  background: #d6fbff;
  color: #000;
  display: inline-block;
  font-size: 12px;
  outline: 0;
  padding: 4px 5px;
  min-width: 30px !important;
  border-radius: 3px;
}
.temp-use:hover {
  background: #00a5b6;
  color: #fff;
  border-radius: 3px;
  cursor: pointer;
}
.template__tab table td {
  padding: 2px 5px !important;
  font-size: 12px;
}
.template__tab table{
  background-color: #F7FAFC
}
