.project-btn {
  min-width: 160px !important;
  right: 0 !important;
  top: 100% !important;
  left: inherit !important;
  background-color: #fff !important;
  border: 1px solid rgba(0, 0, 0, .05);
  border-radius: 0.25rem;
}
.cube-icon{
  font-size: 18px;
}
.project-btn li{
  padding: 3px 10px;
}
.project-btn li span{
  font-size: 15px;
  margin-left: 5px;
  display: inline-block;
  color: #000;
}
.project-btn li i{
  color: #000;
  margin-top: 3px;
}
.project-btn li:hover{
  background-color: #c0c8cf;
}

.logo-image{
  width: 120px !important;
}
.logo-image:hover{
  cursor: pointer;
}
.logo-image img{
  height: 33px;
}