/* This is just a basic example. You can customize it further as needed. */

/* Overlay */
.isLoading::before {
    opacity: 1;
    content: "";
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgb(121, 113, 113) !important;
    z-index: 9999;
  }
  
  /* Loading spinner */
  .isLoading::after {
    content: "";
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border: 6px solid #f3f3f3; /* Light gray */
    border-top: 6px solid #3498db; /* Blue */
    border-radius: 50%;
    width: 60px;
    height: 60px;
    animation: spin 2s linear infinite;
    z-index: 10000;
  }
  
  @keyframes spin {
    0% { transform: translate(-50%, -50%) rotate(0deg); }
    100% { transform: translate(-50%, -50%) rotate(360deg); }
  }

  .spinner {
    border: 8px solid rgba(255, 255, 255, 0.3);
    border-radius: 50%;
    border-top: 8px solid #3498db; /* Blue color for the top border */
    width: 40px;
    height: 40px;
    animation: spin 1s linear infinite;
    margin: 10px auto; /* Adjust margin as needed */
  }
  
  @keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }

/* Adding Css for hyperlink modal */
  .hyperlink-modal {
    width: 300px;
    background: green;
    border: 1px solid #e5e5e5;
    border-radius: 8px;
    position: relative;
    z-index: 1;
    padding: 10px;
  }
